.react-container {
	padding: 0 12px;
	margin: 15px 0 0 0;
}

.react-container .react-icons {
	display: flex;
	padding: 0 20px 0 0;
	justify-content: space-between;
}

/* react-icons left start  */
.react-container .react-icons .left {
	display: flex;
	position: relative;
}

.react-container .react-icons i {
	width: 50px;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #738290;
	box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	transition: all 0.1s;
	font-size: 25px;
	cursor: pointer;
}

.react-container .react-icons .left i {
	margin: 0 0 0 20px;
}

/* react style start  */
.react-icons .left span i img {
	width: 70%;
	height: 70%;
	object-fit: cover;
	object-position: center;
}

.react-icons .left span #like {
	animation: like_react 0.5s ease-in-out forwards;
}

.react-icons .left span #love {
	animation: love_react 0.5s ease-in-out forwards;
}

.react-icons .left span #wow {
	animation: wow_react 0.5s ease-in-out forwards;
}

.react-icons .left span #haha {
	animation: haha_react 0.5s ease-in-out forwards;
}

.react-icons .left span #clap {
	animation: clap_react 0.5s ease-in-out forwards;
}

.react-icons .left span #appreciate {
	animation: appreciate_react 0.5s ease-in-out forwards;
}

.react-icons .left span #dislike {
	animation: dislike_react 0.5s ease-in-out forwards;
}

@keyframes like_react {
	0% {
		transform: scale(0.5);
	}
	50% {
		transform: scale(1.3);
	}
	100% {
		transform: scale(1);
	}
}

