.react-container .react-icons .left label i:hover {
	color: #006d77;
	transform: scale(0.9);
}

.react-container .react-icons .left .share i:hover {
	color: #003049;
	transform: scale(0.9);
}

/* react-icons left end  */

/* react-icons right start  */
.react-container .react-icons .right i:hover {
	color: #ef476f;
	transform: scale(0.9);
}

.react-container .react-icons .right #fill-bookmark {
	color: #ef476f;
}

.react-container .react-icons .right #fill-bookmark.active {
	animation: bookmark 0.5s;
}

.react-container .react-icons .right #empty-bookmark.inactive {
	animation: non-bookmark 0.5s;
}

@keyframes bookmark {
	0% {
		transform: scale(0.5);
	}
	50% {
		transform: scale(1.3);
	}
	100% {
		transform: scale(1);
	}
}

