/* post-container start  */
.post-container {
	margin: 20px 0 20px 0;
	border-radius: 1rem;
	background-color: white;
	padding-bottom: 15px;
}

.post-container:first-child {
	margin: 0 0 20px 0;
}

/* attachment section start  */
.post-container .attachment-main-container {
	margin: 12px 0 0 0;
	padding: 0 12px;
	width: 100%;
}
/* attachment section end  */

#empty-post-message {
	margin: 0;
	font-weight: 500;
	text-align: center;
}
/* post-container end  */
